export default {
  layout: 'admin',
  data() {
    return {
      results: [],
      listLoading: false,
      page: {},
      filters: {
        status: '0'
      },
      selectedRows: [],
      addForm: {
        forumId: '',
        userId: '',
        title: '',
        content: '',
        status: '',
        createTime: ''
      },
      addFormVisible: false,
      addFormRules: {},
      addLoading: false,

      editForm: {
        id: '',
        forumId: '',
        userId: '',
        title: '',
        content: '',
        status: '',
        createTime: ''
      },
      editFormVisible: false,
      editFormRules: {},
      editLoading: false
    }
  },
  mounted() {
    this.list()
  },
  methods: {
    list() {
      const me = this
      me.listLoading = true
      const params = Object.assign(me.filters, {
        page: me.page.page,
        limit: me.page.limit
      })
      this.$axios
        .post('/api/admin/topic/list', params)
        .then((data) => {
          me.results = data.results
          me.page = data.page
        })
        .finally(() => {
          me.listLoading = false
        })
    },
    handlePageChange(val) {
      this.page.page = val
      this.list()
    },
    handleLimitChange(val) {
      this.page.limit = val
      this.list()
    },
    handleAdd() {
      this.addForm = {
        name: '',
        description: ''
      }
      this.addFormVisible = true
    },
    addSubmit() {
      const me = this
      this.$axios
        .post('/api/admin/topic/create', this.addForm)
        .then((data) => {
          me.$message({ message: '提交成功', type: 'success' })
          me.addFormVisible = false
          me.list()
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    handleEdit(index, row) {
      const me = this
      this.$axios
        .get(`/api/admin/topic/${row.id}`)
        .then((data) => {
          me.editForm = Object.assign({}, data)
          me.editFormVisible = true
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    editSubmit() {
      const me = this
      this.$axios
        .post('/api/admin/topic/update', me.editForm)
        .then((data) => {
          me.list()
          me.editFormVisible = false
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    deleteSubmit(row) {
      const me = this
      this.$confirm('是否确认删除该话题?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(function() {
          me.$axios
            .post('/api/admin/topic/delete', { id: row.id })
            .then(function() {
              me.$message({ message: '删除成功', type: 'success' })
              me.list()
            })
            .catch(function(err) {
              me.$notify.error({ title: '错误', message: err.message || err })
            })
        })
        .catch(function() {
          me.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    async undeleteSubmit(row) {
      try {
        await this.$axios.post('/api/admin/topic/undelete', { id: row.id })
        this.list()
        this.$message({ message: '取消删除成功', type: 'success' })
      } catch (err) {
        this.$notify.error({ title: '错误', message: err.message || err })
      }
    },
    async recommend(id) {
      try {
        await this.$axios.post('/api/admin/topic/recommend', {
          id
        })
        this.$message({ message: '推荐成功', type: 'success' })
        this.list()
      } catch (e) {
        this.$notify.error({ title: '错误', message: e.message })
      }
    },
    async cancelRecommend(id) {
      try {
        await this.$axios.delete('/api/admin/topic/recommend', {
          params: {
            id
          }
        })
        this.$message({ message: '取消推荐成功', type: 'success' })
        this.list()
      } catch (e) {
        this.$notify.error({ title: '错误', message: e.message })
      }
    },
    handleSelectionChange(val) {
      this.selectedRows = val
    }
  }
}
